@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,200;0,300;0,400;0,500;0,600;0,900;1,500&display=swap');
.site-branding {
  width: calc(100% - 24px);
  padding: 20px 0;
}
.site-branding a {
  margin-top: 6px;
}
.disableScroll{
  overflow: hidden;
}
.fix{
  position: fixed;
  background: white;
  z-index: 9999;
}
.land{
  position: relative;
  top: 90px;
  z-index: -999;
}
.site-navigation ul {
  position: fixed;
  top: 70px;
  left: -320px;
  z-index: 9999;
  width: 320px;
  max-width: calc(100% - 120px);
  height: 100%;
  padding: 30px 15px;
  margin: 0;
  list-style: none;
  overflow-x: scroll;
  transition: all 0.35s;
}

.site-navigation.show ul {
  left: 0;
  width: 100%;
  max-width: 100%;
}

.site-navigation ul li {
  margin: 0 24px;
}

.site-navigation ul li a {
  display: block;
  padding: 16px 0;
  font-size: 16px;
  font-weight: 600;
  line-height: 1;
  color: #404040;
  text-decoration: none;
  transition: color .35s;
}

.blueImage{
  position: relative;
  top:-35px;
  z-index: -4;
}
.mission{
  border-radius: 60px;
  background: white;
  padding: 9% 5% 13%;
  border: none;
  font-family: 'poppins',sans-serif;
  margin-bottom: 35px;

}
.missionContainer{
  position: relative;
  top:30px;
  z-index: 99;
}
.shiftCloser{
  position: relative;
  left: -120px;
  top:10px
}
.shiftCloser1{
  position: relative;
  left: -70px;
  top:80px
}
.shiftCloser2{
  position: relative;
  left: -100px;
  top:40px

}
.webShow{
  position: relative;
  top:-100px;
}
.box-shadow{
  box-shadow: 0 0 4rem black !important;
  width: 90%;
}.welcomeImage{
  margin-left: -100px;
 }
 .work-t{
   font-family: 'poppins',sans-serif;
   color: white;
   padding: 3px;
 }
.carousel-indicators{
  position:absolute;
  top:0;
  bottom:0;
  margin:auto;
  left:auto;
  width:auto;
}
.carousel-indicators li{width: 20px;
  position:relative;
  border:3px solid #470804;
  height: 19px;
  margin-bottom:11px;
  border-radius: 100%;
  background: gray;
  transition: background 0.25ms ease-in-out;
}
.carousel-indicators li.active{background:white;
}
@media screen and (min-width: 992px) {
  .site-navigation ul {
    position: static;
    top: auto;
    left: auto;
    width: 100%;
    max-width: 100%;
    height: auto;
    padding: 0;
    overflow: auto;
    background: transparent;
  }

  .welcomeImage{

    position: relative;
    top:-170px;
    left:-150px;
    z-index: -2;
    margin-bottom: -95px;
  }

  .blueImage{
    position: relative;
    top:-210px;
    z-index: -3
  }
  .leftBarContact{
    position: absolute;
    left: 600px
  }
  .site-navigation{
    position: relative;
    left: 250px;
  }
  .fix{
    position: static;
    background: none;
    z-index: 0;
  }
  .land{
    position: static;
    top: 0px;
    z-index: 0;
  }
  .missionContainer{
    position: relative;
    top:-455px;
    z-index: 99;
    margin-bottom: -455px;
  }
  .shiftCloser{
    position: relative;
    left: -350px;
    top:100px;
    margin-bottom:100px;
  }
  .shiftCloser1{
    position: relative;
    left: -100px;
    top:400px;
    margin-bottom: 400px;
  }
  .shiftCloser2{
    position: relative;
    left: -300px;
    top:250px;
    margin-bottom: 250px;
  }
  .webShow{
    position: relative;
    top:-500px;
    margin-bottom: -500px;
  }
  .webShow1{
    position: relative;
    top:-200px;
    margin-bottom: -200px;
  }
  .inputGuide{
    height: 85px;
    width: 500px;
  }
  .box-shadow{
    box-shadow: 0 0 4rem black !important;
    width: 50%;
  }
}

@media screen and (min-width: 992px){
  .site-branding {
    width: auto;
  }
}
/*
  Hamburger Menu
----------------------------------------*/
.hamburger-menu {
  position: fixed;
  width: 24px;
  height: 22px;
  right: 5%;
  margin: 0 auto;
  transition: .5s ease-in-out;
  cursor: pointer;
}

.hamburger-menu span {
  display: block;
  position: absolute;
  height: 2px;
  width: 100%;
  background: #262626;
  border-radius: 10px;
  opacity: 1;
  left: 0;
  transition: .25s ease-in-out;
}

.hamburger-menu span:nth-child(1) {
  top: 2px;
}

.hamburger-menu span:nth-child(2),
.hamburger-menu span:nth-child(3) {
  top: 10px;
}

.hamburger-menu span:nth-child(4) {
  top: 18px;
}

.hamburger-menu.open span:nth-child(1) {
  top: 18px;
  width: 0;
  left: 50%;
}

.hamburger-menu.open span:nth-child(2) {
  transform: rotate(45deg);
}

.hamburger-menu.open span:nth-child(3) {
  transform: rotate(-45deg);
}

.hamburger-menu.open span:nth-child(4) {
  top: 18px;
  width: 0;
  left: 50%;
}
